#kezdolap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url("../../images/landing_bg.jpg");
    width: 100%;
    min-height: 100vh;
    color: #010252;
}

#kezdolap .landingContent {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: calc(100vh - 180px);
}

#kezdolap .landingContent .landingTextImg {
    display: flex;
    align-items: center;
}

#kezdolap .landingContent .landingTextImg img {
    height: 300px;
    width: 300px;
}

#kezdolap .landingContent .landingTextImg .landingText {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

#kezdolap .landingContent .landingTextImg .landingText .landingTitle {
    width: 750px;
    font-size: 55px;
}

#kezdolap .landingContent .landingTextImg .landingText .landingSubtitle {
    width: 750px;
    font-size: 22px;
    font-style: italic;
    font-weight: 400;
}

@media (max-width: 1700px) {  
    #kezdolap {
        padding-top: 80px;
    }

    #kezdolap .landingContent .landingTextImg {
        flex-direction: column;
        gap: 40px;
    }

}

@media (max-width: 1280px) {  
    #kezdolap {
        padding-top: 100px;
    }

    #kezdolap .landingContent {
        flex-direction: column;
        gap: 70px;
        height: unset;
    }

    #kezdolap .landingContent .landingTextImg {
        text-align: center;
        gap: 10px;
    }

    #kezdolap .landingContent .landingTextImg img {
        height: 200px;
        width: 200px;
    }
}

@media (max-width: 820px) {  
    
    #kezdolap nav {
        padding: 0 10px;
        font-size: 16px;
    }

    #kezdolap nav ul {
        gap: 50px;
    }

    #kezdolap .landingContent .landingTextImg .landingText .landingTitle, #kezdolap .landingContent .landingTextImg .landingText .landingSubtitle {
        text-align: center;
        width: 80%;
    }

    #kezdolap .landingContent .landingTextImg .landingText .landingTitle {
        font-size: 35px;
    }

    #kezdolap .landingContent .landingTextImg .landingText .landingSubtitle {
        font-size: 18px;
    }
}

@media (max-width: 450px) {  
    
    #kezdolap nav {
        gap: 20px;
        padding: 0 10px;
        font-size: 12px;
    }

    #kezdolap nav ul {
        gap: 20px;
    }

    #kezdolap .landingContent {
        gap: 20px;
    }

    #kezdolap .landingContent .landingTextImg .landingText .landingTitle {
        font-size: 22px;
    }

    #kezdolap .landingContent .landingTextImg .landingText .landingSubtitle {
        font-size: 14px;
    }
    
    #kezdolap .landingContent .landingTextImg img {
        height: 120px;
        width: 120px;
    }
}